@import "src/styles/variables";

.header {
  &__container {
    display: flex;
    align-items: center;
    flex-direction: column;
    @media (min-width: $md) {
      flex-direction: row;
    }
  }

  &__logo-wrapper {
    display: flex;
    @media (min-width: $md) {
      flex-direction: column;
      margin-right: 20px;
    }
    @media (min-width: $lg) {
      flex-direction: row;
      margin-right: 30px;
    }
    @media (min-width: $xl) {
      margin-right: 100px;
    }
  }

  &__phone-time-wrapper {
    display: flex;
    align-items: center;
    flex-direction: column;
    @media (min-width: $lg) {
      flex-direction: row;
      justify-content: center;
    }
  }

  &__registry-basket-wrapper {
    display: flex;
    overflow: hidden;
    @media (min-width: $md) {
      margin-left: 20px;
    }
    @media (min-width: $lg) {
      margin-left: 30px;
    }
    @media (min-width: $xl) {
      margin-left: 100px;
    }
  }
}